import React from 'react';
import 'chart.js/auto';
import { Bar } from 'react-chartjs-2';
import { Box, Button, Card, CardContent, CardHeader, Divider, useTheme } from '@mui/material';
import ArrowRightIcon from '@mui/icons-material/ArrowRight';

export const TasksGraph = (props) => {
  const theme = useTheme();

  const options = {
    animation: false,
    cornerRadius: 20,
    layout: { padding: 0 },
    legend: { display: false },
    maintainAspectRatio: false,
    responsive: true,
    xAxes: [
      {
        ticks: {
          fontColor: theme.palette.text.secondary,
        },
        gridLines: {
          display: false,
          drawBorder: false,
        },
      },
    ],
    yAxes: [
      {
        ticks: {
          fontColor: theme.palette.text.secondary,
          beginAtZero: true,
          min: 0,
        },
        gridLines: {
          borderDash: [2],
          borderDashOffset: [2],
          color: theme.palette.divider,
          drawBorder: false,
          zeroLineBorderDash: [2],
          zeroLineBorderDashOffset: [2],
          zeroLineColor: theme.palette.divider,
        },
      },
    ],
    tooltips: {
      backgroundColor: theme.palette.background.paper,
      bodyFontColor: theme.palette.text.secondary,
      borderColor: theme.palette.divider,
      borderWidth: 1,
      enabled: true,
      footerFontColor: theme.palette.text.secondary,
      intersect: false,
      mode: 'index',
      titleFontColor: theme.palette.text.primary,
    },
  };

  const [data, setData] = React.useState({ datasets: [], labels: [] });

  React.useEffect(() => {
    const dates = Object.keys(props.data);
    const datasets = [
      {
        backgroundColor: '#eee',
        barPercentage: 0.5,
        barThickness: 12,
        borderRadius: 4,
        categoryPercentage: 0.5,
        data: Object.keys(props.data).map((date) => props.data[date].draft),
        label: 'Brouillon',
        maxBarThickness: 10,
      },
      {
        backgroundColor: '#2E7D32',
        barPercentage: 0.5,
        barThickness: 12,
        borderRadius: 4,
        categoryPercentage: 0.5,
        data: Object.keys(props.data).map((date) => props.data[date].confirm),
        label: 'Confirmer',
        maxBarThickness: 10,
      },
      {
        backgroundColor: '#D32F2F',
        barPercentage: 0.5,
        barThickness: 12,
        borderRadius: 4,
        categoryPercentage: 0.5,
        data: Object.keys(props.data).map((date) => props.data[date].cancel),
        label: 'Cancel',
        maxBarThickness: 10,
      },
    ];
    setData({ datasets, labels: dates });
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [props.data]);

  // const data = {
  //   datasets: [
  //     {
  //       backgroundColor: '#3F51B5',
  //       barPercentage: 0.5,
  //       barThickness: 12,
  //       borderRadius: 4,
  //       categoryPercentage: 0.5,
  //       data: [18, 5, 19, 27, 29, 19, 20],
  //       label: 'This year',
  //       maxBarThickness: 10,
  //     },
  //     {
  //       backgroundColor: '#EEEEEE',
  //       barPercentage: 0.5,
  //       barThickness: 12,
  //       borderRadius: 4,
  //       categoryPercentage: 0.5,
  //       data: [11, 20, 12, 29, 30, 25, 13],
  //       label: 'Last year',
  //       maxBarThickness: 10,
  //     },
  //   ],
  //   labels: ['1 Aug', '2 Aug', '3 Aug', '4 Aug', '5 Aug', '6 Aug', '7 aug'],
  // };

  return (
    <Card {...props}>
      <CardHeader title={props.title} />
      <Divider />
      <CardContent>
        <Box
          sx={{
            height: 400,
            position: 'relative',
          }}
        >
          <Bar data={data} options={options} />
        </Box>
      </CardContent>
      <Divider />
      <Box
        sx={{
          display: 'flex',
          justifyContent: 'flex-end',
          p: 2,
        }}
      >
        <Button color="primary" endIcon={<ArrowRightIcon fontSize="small" />} size="small">
          Overview
        </Button>
      </Box>
    </Card>
  );
};
